<template>
   <div class="sanfang">
       <Bank />
       <div class="sanfang01">
           <van-radio-group v-model="radio">
                <van-radio v-for="(item,index) in mList" :key="index" :name="item" >{{ item }} <span style="font-size: 12px;"> 元 </span> </van-radio>
            </van-radio-group>
       </div>


       <div class="sanfang02">
           <p> {{ `*支付宝` }} </p>
           <van-field v-model="radio" type="number" label="充值金额" placeholder="请输入充值金额" clearable />
           <van-cell :value="tongdao" is-link @click="show = true">
            <!-- 使用 title 插槽来自定义标题 -->
            <template slot="title">
                <span class="custom-title">充值通道</span>
            </template>
            </van-cell>
       </div> 

       <br>
       <van-button round block type="danger" @click="sub"> 确认支付 （￥{{ radio }} 元 ） </van-button>

        <van-action-sheet v-model="show" :actions="actions" @select="onSelect" description="请选择一个充值渠道" />
   </div>
</template>
<script>
import Bank from '@/components/NavBar/bank.vue'
   export default { 
      name: "sanfang",
      components: {Bank},
      data() {
         return {
            //  金额列表
            mList:[ '10','50','100','500','1000','2000','10000','50000' ],
            //  充值金额
             radio: '10',
            //  通道选择框状态
             show: false,
            //  通道列表
            actions: [
                { name: '支付通道001', subname: '充值金额（100 - 50000）' },
                { name: '支付通道002', subname: '充值金额（10 - 10000）' },
                { name: '支付通道003', subname: '充值金额（10000 - 50000）' }
            ],
            // 选中的通道名字
            tongdao:"请选择一个充值渠道"
         }
      },
      computed: {},
      mounted: function() {},
      methods: {
          onSelect(item) {
            // 默认情况下点击选项时不会自动收起
            // 可以通过 close-on-click-action 属性开启自动收起
            this.show = false;
            this.$toast(item.name);
            this.tongdao = item.name;
        },
        sub(){
            this.$toast('跳转第三方支付');
            // this.$router.go(-1);
        }
      }
   }
</script>
